      // console.log(response.data);
      // console.log(response.status);
      // console.log(response.headers);
      // console.log(response.statusText);
      switch (response.status) {
        case 400:
          MessageBox.confirm("å®¢æˆ·ç«¯è¯·æ±‚çš„è¯­æ³•é”™è¯¯ï¼ŒæœåŠ¡å™¨æ— æ³•ç†è§£", "Error");
          break;
        case 401:
          // è¿”å›ž 401 æ¸…é™¤tokenä¿¡æ¯å¹¶è·³è½¬åˆ°ç™»å½•é¡µé¢
          store.commit("removeToken");
          if (router.currentRoute.path !== "login") {
            router.replace({
              path: "login",
              query: { redirect: router.currentRoute.fullPath }
            });
          }
          // MessageBox.confirm("è¦æ±‚ç”¨æˆ·çš„èº«ä»½è®¤è¯", 'Error');
          break;
        case 403:
          MessageBox.confirm("ç™»å½•è¿‡æœŸï¼Œè¯·é‡æ–°ç™»å½•", "Error");
          break;
        case 404:
          Message.warning(response.statusText);
          break;
        default:
          Message({
            type: "error",
            message: response.statusText
          });
          // return Promise.reject(error.response);
          break;
      }

      // } else if (error.request) {
