import Vue from "vue";
import store from "../store";
import Router from "vue-router";
import TheHome from "@/views/TheHome";
import TheLogin from "@/views/TheLogin";
import NotFound from "@/views/NotFound";
import TheLoading from "@/views/TheLoading";
import JsBase from "@/components/jsbase/JsBase";
import BaseTest from "@/components/mytest/BaseTest";
import ApiBase from "@/components/apiopen/ApiBase";

const HelloWorld = () => import("@/components/HelloWorld");
const TheAbout = () => import("@/views/TheAbout");
// js base
const JsBase1 = () => import("@/components/jsbase/JsBase1");
const JsBase2 = () => import("@/components/jsbase/JsBase2");
const JsBase3 = () => import("@/components/jsbase/JsBase3");
// js higher
const JsBaseHigher1 = () => import("@/components/jsbase/JsBaseHigher1");
const JsBaseHigher2 = () => import("@/components/jsbase/JsBaseHigher2");
const JsBaseAjax = () => import("@/components/jsbase/JsBaseAjax");
// my test
const HelloTest = () => import("@/components/mytest/HelloTest");
// api open
const WangYiNews = () => import("@/components/apiopen/WangYiNews");

Vue.use(Router);

/**
 * å‡çº§vue-routerè‡³3.1ä»¥åŽç‰ˆæœ¬
 * åœ¨ä½¿ç”¨Element UI çš„å¯¼èˆªç»„ä»¶el-menuæ—¶é‡å¤ç‚¹å‡»åŒä¸€ä¸ªè·¯ç”±ï¼ŒæŽ§åˆ¶å°æŠ¥é”™NavigationDuplicatedï¼Œä½†ä¸å½±å“ä½¿ç”¨
 * è§£å†³æ–¹æ³•å¦‚ä¸‹ï¼š
 * é‡å†™è·¯ç”±çš„pushæ–¹æ³•
 */
const routerPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error => error);
};

const router = new Router({
  routes: [
    {
      path: "/",
      name: "TheHome",
      component: TheHome
    },
    {
      path: "/login",
      name: "TheLogin",
      component: TheLogin
    },
    {
      path: "/jsBase",
      component: JsBase,
      alias: "/jsBaseh",
      children: [
        {
          path: "jsBase1",
          name: "JsBase1",
          component: JsBase1
        },
        {
          path: "jsBase2",
          name: "JsBase2",
          component: JsBase2
        },
        {
          path: "jsBase3",
          name: "JsBase3",
          component: JsBase3
        }
      ]
    },
    {
      path: "/jsBaseh",
      component: JsBase,
      children: [
        {
          path: "jsBaseHigher1",
          name: "JsBaseHigher1",
          component: JsBaseHigher1
        },
        {
          path: "jsBaseHigher2",
          name: "JsBaseHigher2",
          component: JsBaseHigher2
        },
        {
          path: "ajax",
          name: "JsBaseAjax",
          component: JsBaseAjax
        }
      ]
    },
    {
      path: "/test",
      component: BaseTest,
      children: [
        {
          path: "test1",
          name: "HelloTest",
          component: HelloTest
        }
      ]
    },
    {
      path: "/about",
      name: "TheAbout",
      component: TheAbout,
      meta: {
        requireAuth: true // æ·»åŠ è¯¥å­—æ®µï¼Œè¡¨ç¤ºè¿›å…¥è¿™ä¸ªè·¯ç”±æ˜¯éœ€è¦ç™»å½•çš„
      }
    },
    {
      path: "/hello",
      name: "HelloWorld",
      component: HelloWorld,
      meta: {
        requireAuth: true // æ·»åŠ è¯¥å­—æ®µï¼Œè¡¨ç¤ºè¿›å…¥è¿™ä¸ªè·¯ç”±æ˜¯éœ€è¦ç™»å½•çš„
      }
    },
    {
      path: "/loading",
      name: "TheLoading",
      component: TheLoading,
      meta: {
        requireAuth: true // æ·»åŠ è¯¥å­—æ®µï¼Œè¡¨ç¤ºè¿›å…¥è¿™ä¸ªè·¯ç”±æ˜¯éœ€è¦ç™»å½•çš„
      }
    },
    {
      path: "/apiopen",
      component: ApiBase,
      children: [
        {
          path: "wynews",
          name: "WangYiNews",
          component: WangYiNews
          //   },
          //   {
          //     path: "jsBaseHigher2",
          //     name: "JsBaseHigher2",
          //     component: JsBaseHigher2
          //   },
          //   {
          //     path: "ajax",
          //     name: "JsBaseAjax",
          //     component: JsBaseAjax
        }
      ]
    },
    {
      path: "*",
      name: "NotFound",
      component: NotFound
    }
  ]
});
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requireAuth)) {
    if (!store.state.AUTH_TOKEN) {
      next({
        path: "/login",
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      next();
    }
  } else {
    next();
  }
});
export default router;
