<template>
  <el-form status-icon :model="myData" :rules="myRules" ref="myForm" label-width="100px" class="myForm">
    <el-form-item label="å¯†ç " prop="pass">
      <el-input v-model="myData.pass" autocomplete="off" type="password"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass" label="ç¡®è®¤å¯†ç ">
      <el-input v-model="myData.checkPass" type="password" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item prop="age" label="å¹´é¾„">
      <el-input autocomplete="off" v-model.number="myData.age"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('myForm')">æäº¤</el-button>
      <el-button @click="resetForm('myForm')">é‡ç½®</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: "TheLogin",
  data() {
    const checkAge = (rule, value, callback) => {
      console.info(rule);
      if (!value) {
        return callback(new Error("å¹´é¾„ä¸èƒ½ä¸ºç©º"));
      }
      // setTimeout(()=>{},1000); å±•ç¤ºéªŒè¯è¿‡ç¨‹ï¼Œè§†è§‰æ•ˆæžœå¥½
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          return callback(new Error("è¯·è¾“å…¥æ•´æ•°"));
        } else {
          if (value < 18) {
            return callback(new Error("å¿…é¡»å¹´æ»¡18å‘¨å²"));
          } else {
            callback();
          }
        }
      }, 1000);
    };
    const validatePass = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error("è¯·è¾“å…¥å¯†ç "));
      } else {
        // å¦ä¸€ä¸ªå·²è¾“å…¥ï¼Œä¿®æ”¹æœ¬èº«ï¼Œåœ¨éªŒè¯
        if (this.myData.checkPass !== "") {
          this.$refs.myForm.validateField("checkPass");
        }
        callback();
      }
    };
    const validatePass2 = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error("è¯·å†æ¬¡è¾“å…¥å¯†ç "));
      } else if (value !== this.myData.pass) {
        return callback(new Error("ä¸¤æ¬¡è¾“å…¥çš„å¯†ç ä¸ä¸€è‡´"));
      } else {
        callback();
      }
    };
    return {
      myData: {
        pass: "",
        checkPass: "",
        age: ""
      },
      myRules: {
        pass: [{ trigger: "blur", validator: validatePass }],
        checkPass: [{ trigger: "blur", validator: validatePass2 }],
        age: [{ trigger: "blur", validator: checkAge }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      const that = this;
      that.$refs[formName].validate(valid => {
        if (valid) {
          const params = {
            pass: that.myData.pass,
            checkPass: that.myData.checkPass,
            age: that.myData.age
          };
          that.$ajax.application
            .login(params)
            .then(res => {
              console.log("res");
              if (res.data) {
                console.log(res);
                if (res.data.token) {
                  that.$store.commit("setToken", res.data.token);
                }
                console.log(that.$route);
                const redirectUrl = decodeURIComponent(that.$route.query.redirect || "/");
                console.log(redirectUrl);
                that.$store.commit("setActiveIndex", redirectUrl);
                that.$router.push(redirectUrl);
              } else {
                that.$message({
                  type: "error",
                  message: res.statusText
                });
              }
              console.log("res");
            })
            .catch(error => {
              console.warn(error);
              that.$message({
                type: "error",
                message: error.statusText
              });
            });
          // alert("submit")
          // } else {
          //     console.warn("error");
          //     that.$message({
          //         type: 'error',
          //         message: response.statusText
          //     });
          //     return false
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style scoped>
.myForm {
  width: 460px;
  margin: 40px auto 0;
}
</style>
